#clock-container {
  width: 47%;
  height: 55%;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 3rem;
  margin-right: 20px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}


h2 {
  font-size: min(32px, 12vw);
}

#time-left {
  font-size: min(80px, 20vw);
  color: #410083;
}

#control-panel {
  width: min(45%, 130px);
  display: flex;
  justify-content: space-between;
}

#control-panel button {
  background: none;
  border: none;
  color: #410083;
  font-size: min(40px, 10vw);
  cursor: pointer;
}

.is-finishing {
  color: #ff1414;
}

#timer-card{
  border:none;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
#about-card {
  width: 90%;
  margin: auto;
  margin-top: 1rem;
  padding: 1rem;
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  border-color: #8c7b8ed5; 
  box-shadow: 0px 0px 98px 15px rgba(0,0,0,0.09);
}

#about-btn-div {
  display: flex;
  justify-content: space-around;
}

#custom-btn {
  background-color: #8e5994;
  margin: 0.5%;
  color: whitesmoke;
  --bs-btn-border-color: #dbc3de;
}

#custom-btn:hover {
  background-color: #9f87a2;
}

#custom-btn:active {
  background-color: #9f87a2;
}

#about-header {
  font-size: x-large;
  text-align: center;
}

#about-p {
  margin: auto;
  width: 90%;
  text-align: center;
  display: flex;
  /* align-items: center; */
}

